const getShunsuikenXtraPctDmg = (char, selfMCs) => {
  return checkCharMC(Ayato.buffs[3], char, selfMCs.BCs, 3) ? 30 : 0;
};

const getShunsuikenTlBnes = ({ char, selfMCs, ATTRs, party }) => {
  const level =
    char["Elemental Skill"] +
    getXtraTalentLv(Ayato.actvTalents[1], char, party);
  return {
    xtraDmgBn: getShunsuikenXtraPctDmg(char, selfMCs),
    xtraFlat: checkCharMC(Ayato.buffs[0], char, selfMCs.BCs, 0, true)
      ? Math.round(ATTRs.HP * 0.0044 * TalentLvMults[1][level])
      : 0
  };
};

